import { Component, EventEmitter, Input, NgZone, OnChanges, Output, TemplateRef, ViewChild } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry } from 'ngx-file-drop';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';

@Component({
  selector: 'ibss-editabout2',
  templateUrl: './editabout2.component.html',
  styleUrls: ['./editabout2.component.scss']
})
export class Editabout2Component implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputParam: any;
  @Input() index: any;
  @Input() isView: boolean= false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  config:typeof CONFIG = CONFIG;
  editItem:any = null;
  activeColor:any =null;
  modalRef: BsModalRef;
  imgIndx: any = -1;
  @ViewChild('cropImage') cropImage!: TemplateRef<any>; 
  
  imageFile: any = '';
  croppedImage: any = '';
  constructor(
    private ngZone:NgZone,
    private toastr: ToastrService,
    private modalService: BsModalService
  ) { }

  ngOnChanges(): void {
    console.log("EditIntro2Component", this.inputParam);
    this.editItem = JSON.parse(JSON.stringify(this.inputParam));

    this.editItem.events.map((event:any)=>{
      event.date = new Date(event.date);
      event.time = new Date(event.time);
      return event;
    })
  }

  updateEditor(close:any) {
    this.inputParam = JSON.parse(JSON.stringify(this.editItem));
    const params: any = {
      component:JSON.parse(JSON.stringify(this.inputParam)),
      action:"saveEdit",
      index:this.index,
      close:close
    };
    this.onAction.emit(params)
  }

  public files: NgxFileDropEntry[] = [];

  public dropped(files: NgxFileDropEntry[], index:any) {
    this.files = files;
    for (const droppedFile of files) {

      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {

          // Here you can access the real file
          console.log(droppedFile.relativePath, file);

          const filename = file.name.split(".");
          const ext = filename[filename.length-1].toLowerCase();
          
          if (this.config.imageFormats.indexOf(ext) !==-1) {
              // this.onCompressImage(file);
          this.readURL(file, index);
            // this.imageFile = file;
            // this.onCropImage(index);
          } else {
            this.toastr.error("Invalid file format. Only png, gif and jpg formats are allowed");
          }

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  readURL(file: any, index:any): void {
    if (file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.editItem.events[index].image = JSON.parse(JSON.stringify(reader.result));
      };
      reader.readAsDataURL(file);
    }
  }

  selectColor(key:any) {
    this.activeColor = {
      key:key,
      value:JSON.parse(JSON.stringify(this.editItem.colors[key]))
    }
  }

  changeComplete(color:any) {
    console.log("changeComplete", color);
    this.activeColor.value = color.color.hex;
    this.editItem.colors[this.activeColor.key] = JSON.parse(JSON.stringify(this.activeColor.value));
    this.updateEditor(false);
  }
  
  onSaveColor() {
    this.activeColor = null;
  }

  onValueChange(event:any) {
    console.log(event);
    console.log(this.editItem);
  }

  onAddEvent() {
      this.editItem.events.push({
        name:"Wedding Party",
        image:"../../../../../assets/images/web/layout2/wedding-party.jpg",
        location:"Church End, Hendon, London, UK",
        date:new Date(),
        time:new Date(),
        description:"Event Description"
      })
  }

  onDeleteEvent(index:any) {
    this.editItem.events.splice(index,1);
    this.updateEditor(false); 
  }

  onCropImage(index:any) {
    this.imgIndx = index;
    this.modalRef = this.modalService.show(this.cropImage, {class: 'modal-lg'});
  }

  saveCropped(): void {
    this.editItem.events[this.imgIndx].image = JSON.parse(JSON.stringify(this.croppedImage));
    this.imageFile = null;
    this.imgIndx = -1;
    this.croppedImage = null;
    this.modalRef.hide();
  }
 

  cancelCrop(): void {
    this.modalRef.hide();
    this.imageFile = null;
    this.imgIndx = -1;
    this.croppedImage = null;
  }

  onInvalidImage() {
    this.modalRef.hide();
    this.toastr.error("Invalid file format, only png, gif and jpg are allowed");
  }

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64;
  }
}
