import { Component, NgZone, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from '../config';
import { WIZARD_CONFIG, IBSSMAINITEM, IBSSDEFAULTITEM } from './wizard.config';
import { WizardService } from './wizard.service';
import { Location } from '@angular/common';

declare var $: any;

@Component({
  selector: 'app-wizard',
  templateUrl: './wizard.component.html',
  styleUrls: ['./wizard.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class WizardComponent implements OnInit {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  item: IBSSMAINITEM = new IBSSDEFAULTITEM();
  addSectionField: any = {
    title: "",
    isNav: false,
    navType: "",
    navParent: ""
  }
  header1Content: any;
  intro1Content: any = {
    id: "IBSSCOMMONINTRO1",
    content: []
  }
  about1Content: any;
  gallery1Content: any;
  contact1Content: any;
  footer1Content: any;
  peoples1Content: any;
  isNew = true;
  profileData: any;
  profileName: any;
  mainMenus:any = [];
  steps = [
    {
      title:"Basic details",
      component:"basic_details",
      canEdit:true,
      hasError:true,
    },
    {
      title:"Site Configuration",
      component:"site_config",
      canEdit:false,
      hasError:true,
    },
    {
      title:"Edit Content",
      component:"edit_content",
      canEdit:false,
      hasError:true,
    }
    // {
    //   title:"Preview",
    //   component:"preview"
    // }
  ]

  editComponent:any =null;
  editIndx = -1;

  constructor(
    private ws: WizardService,
    public toastr: ToastrService,
    private ngZone: NgZone,
    private router: Router,
    private route: ActivatedRoute,
    private location: Location
  ) { }

  ngOnInit() {
    const routeParams = this.route.snapshot.paramMap;
    this.profileName = routeParams.get('Id');

    if (this.profileName) {
      this.config.toggleLoader(true);
      this.ws.getViewData(this.profileName + "/1")
        .subscribe((res: any) => {
          if (res.error && res.error.responseCode === 0) {
            this.profileData = res.profileData ? res.profileData : null;
            this.item = JSON.parse(JSON.stringify(this.profileData));
            this.isNew = false;
            this.config.toggleLoader(false);
            this.mainMenus = this.formMenuItems(this.item.pages);
            this.checkCanEdit();
          } else {
            this.config.toggleLoader(false);
            this.toastr.error("No profile found with name - " + this.profileName);
            this.router.navigate(['/main']);
          }
        }, (err: any) => {
          this.config.toggleLoader(false);
          this.toastr.error("Oops there was some issue in fetching the profile. Please try again after sometime! - ");
          this.router.navigate(['/main']);
        });
    } else {
      if (this.wiz_config.isMock) {
        this.item = JSON.parse(JSON.stringify(this.wiz_config.IBSSWEBTEMPLATE2));
      }
    }
    this.header1Content = JSON.parse(JSON.stringify(this.wiz_config.header1Content));
    this.intro1Content.content = JSON.parse(JSON.stringify(this.wiz_config.intro1Items));
    this.about1Content = JSON.parse(JSON.stringify(this.wiz_config.about1Content));
    this.gallery1Content = JSON.parse(JSON.stringify(this.wiz_config.gallery1Content));
    this.contact1Content = JSON.parse(JSON.stringify(this.wiz_config.contact1Content));
    this.footer1Content = JSON.parse(JSON.stringify(this.wiz_config.footer1Content));
    this.peoples1Content = JSON.parse(JSON.stringify(this.wiz_config.peoples1Content));

    this.mainMenus = this.formMenuItems(this.item.pages);
    
    console.log("WizardComponent", this.item);
  }

  handleSteps(evt: any) {
    console.log("handleSteps", evt);
    this.ngZone.run(()=>{ 

      if (evt.action === "edit") {
        this.editComponent = JSON.parse(JSON.stringify(evt.component));
        this.editIndx = evt.index;
      } else if (evt.action === "saveEdit") {
        this.item.pages[evt.index].template = JSON.parse(JSON.stringify(evt.component));
        if (evt.close) {
          this.editComponent = null;
          this.editIndx =-1;
          this.saveAction("MODIFY", this.item);
        }
      } else  if (evt.action === "localSave") {
        if (evt.component) {
          this.item.pages[evt.index] = JSON.parse(JSON.stringify(evt.component));
        } else {
          this.item = JSON.parse(JSON.stringify(evt.inputParam));
        }
        this.saveAction("MODIFY", this.item);
      }  else  if (evt.additonalAction === "preview") {
        this.item = JSON.parse(JSON.stringify(evt.inputParam));
        this.saveAction("MODIFY", this.item, true);
      } else if (evt.action !== "previous" && evt.action !== "next") {
        this.item = JSON.parse(JSON.stringify(evt.inputParam));
        console.log(evt.action, this.item);
        this.saveAction(evt.action, this.item);
      }
    });
    this.checkCanEdit();
  }

  saveAction(action: any, item: any, preview = false) {
  
    item.status = action;
    let inputPayload = {
      "serviceData": {
        "profileData": item,
        "profileName": item.title,
        "profileType": item.type,
        "profileCategory" : 1,
        "requestType": this.isNew ? "CREATE" : item.status
      }
    }
    console.log("saveAction",action, item);
    // return;
    this.config.toggleLoader(true);
    this.ws.save(inputPayload)
      .subscribe((res: any) => {
        console.log('signup success', res);
        this.config.toggleLoader(false);
        if (res.error.responseCode == 0) {
          const msg = item.status === "PUBLISH" ? "Published successfully!!!" : "Saved Successfully!!!";
          this.toastr.success(msg);
          this.isNew = false;
          this.location.replaceState('./main/edit/'+ item.title);


          if (item.status === "PUBLISH") {
            this.router.navigate(['/main']);
          } else {
            this.checkCanEdit();
            if(preview) {
              window.open('../../preview/'+this.item.title, '_blank');
            }
          }
        } else {
          if (res.error.businessValidation.length > 0) {
            for (let i = 0; i < res.error.businessValidation.length; i++) {
              this.toastr.error(res.error.businessValidation[i].errorDesc);
            }
          }

          if (res.error.dataValidation.length > 0) {
            for (let i = 0; i < res.error.dataValidation.length; i++) {
              this.toastr.error(res.error.dataValidation[i].errorDesc);
            }
          }

        }
      }, (error) => {
        console.log('signup failure', error);
        this.config.toggleLoader(false);
        this.toastr.error("Sigup Failed, Please try again after sometimes");
      })
  }

  onCancel() {
    console.log("onCancel", this.ws.modifyIndex);
    this.ws.modifyIndex = -1;
  }

  onDone(component: any, template: any, pos: any) {
    let isValid = true;
    if (this.addSectionField.title && this.addSectionField.title.trim().length == 0) {
      this.toastr.error("Please enter the Section title");
      isValid = false;
    } else if (this.addSectionField.isNav) {
      if (this.addSectionField.navType === "") {
        this.toastr.error("Please select the menu type");
        isValid = false;
      } else if (this.addSectionField.navType === "SubMenu" && this.addSectionField.navParent === "") {
        this.toastr.error("Please select the parent menu");
        isValid = false;
      }
    }
    console.log("onDone", component, pos, this.ws.modifyIndex);

    if (isValid) {
      let item = JSON.parse(JSON.stringify(this.item));

      let indx = pos === "after" ? this.ws.modifyIndex + 1 : this.ws.modifyIndex;
      let d= JSON.parse(JSON.stringify(this.addSectionField));
      let content = { 
        id: component,
        menuId:(component === "headerComponent" || component === "footerComponent") ? "" :d.navParent+"_"+d.title.replace(/\s/g, '')+"_"+indx,
        menuType:(component === "headerComponent" || component === "footerComponent") ? "" :d.navType,
        menuParent:(component === "headerComponent" || component === "footerComponent") ? "" :d.navParent,
        menuTitle:(component === "headerComponent" || component === "footerComponent") ? "" :d.title,
        template: template 
      };

      if (component === "headerComponent") {
        item.pages.unshift(content);
      } else if (component === "footerComponent") {
        item.pages.push(content);
      } else {
        item.pages.splice(indx, 0, content);
      }

      if (!this.canAdd('headerComponent')) {
        item.pages[0].template.pages = this.formMenuItems(item.pages);
      }
      this.item = item;
      this.addSectionField = {
        title: "",
        isNav: false,
        navType: "",
        navParent: ""
      };

      $('#addSectionModal').modal('hide');
      this.ws.modifyIndex = -1;
    }
  }

  onDelete() {
    console.log("onDelete", this.ws.modifyIndex);
    const items = JSON.parse(JSON.stringify(this.item));
    const item: any = items.pages[this.ws.modifyIndex];
    if (item.menuType === "Main") {
      this.ngZone.run(()=>{
        items.pages[0].template.pages.forEach((ele:any, indx:any)=> {
          if (ele.id===item.menuId) {
            items.pages[0].template.pages.splice(indx, 1);
            return;
          }
        });
      });
    } else if (item.menuType === "SubMenu"){
      this.ngZone.run(()=>{
       items.pages[0].template.pages.forEach((ele: any, indx: any)=> {
          ele.subPages.forEach((element: any,indx1: any )=> {
            if (element.id===item.menuId) {
              items.pages[0].template.pages[indx].subPages.splice(indx1, 1);
              return;
            }
          });
        });
      })
    }
    
    items.pages.splice(this.ws.modifyIndex, 1);
    this.ws.modifyIndex = -1;
    
    if (!this.canAdd('headerComponent')) {
      items.pages[0].template.pages = this.formMenuItems(items.pages);
    }

    this.item = items;
  }

  canAdd(component: any) {
    let canAdd = true;
    const filtered = this.item.pages.filter(page => page.id === component);
    return filtered.length == 0;
  }

  formMenuItems(pages: any) {
    let menusItem: any = [];
    const mainMenus = pages.filter((ele:any) => ele.menuType === "Main");
    const subMenus = pages.filter((ele:any) => ele.menuType === "SubMenu" && ele.menuParent !== "");
    const menusId: any = [];
    mainMenus.forEach((page:any) => {
      if (page.id !== "headerComponent" && page.id !== "footerCompnent") {
        menusId.push(page.menuId);
        menusItem.push({
          title: page.menuTitle,
          type: "section",
          id: page.menuId,
          subPages: []
        })
      }
    });

    subMenus.forEach((page: any) => {
      if (page.id !== "headerComponent" && page.id !== "footerCompnent") {
        const mIndx = menusId.indexOf(page.menuParent);
        if (mIndx > -1) {
          menusItem[mIndx].subPages.push({
            title: page.menuTitle,
            type: "section",
            id: page.menuId,
          })
        }
      }
    });

    return menusItem;
  }

  checkCanEdit() {
    // {
    //   title:"Basic details",
    //   component:"basic_details",
    //   canEdit:true,
    //   hasError:true,
    // },
    // {
    //   title:"Site Configuration",
    //   component:"site_config",
    //   canEdit:false,
    //   hasError:true,
    // },
    // {
    //   title:"Edit Content",
    //   component:"edit_content",
    //   canEdit:false,
    //   hasError:true,
    // }
    this.steps.forEach((step: any, index: number) => {
        switch(step.component) {
          case "basic_details":  this.validateBasicDetails(step, index); break;
          case "site_config":  this.validateSiteConfig(step, index); break;
          case "edit_content":  this.validateContent(step, index); break;
        }
    });
  }

  validateBasicDetails(step:any, index: number) {
    if (step && this.item) {
      step.hasError = true;
      const title = this.item.title !== null && this.item.title !== undefined && this.item.title.trim().length >=8;
      const id = this.item.id !== null && this.item.id !== undefined && this.item.id.trim().length >= 8;
      const type = this.item.type !== null && this.item.type !== undefined && this.item.type.trim().length > 0;
      if (title && id && type) {
        step.hasError = false;
        const nextIndx = index+1;

        if (nextIndx < this.steps.length) {
          this.steps[nextIndx].canEdit = true;
        }
      }
    }
  }

  validateSiteConfig(step:any, index: number) {
    if (step && this.item) {
      this.steps[index].hasError = true;
      const layout = this.item.layoutID !== null && this.item.layoutID !== undefined && this.item.layoutID.trim().length > 0;
      const template = this.item.templateID !== null && this.item.templateID !== undefined && this.item.templateID.trim().length > 0;
      if (template) {
        const nextIndx = index+1;
        if (nextIndx < this.steps.length) {
          this.steps[nextIndx].canEdit = true;
          this.steps[index].hasError = false;
        }
      }
    }
  }

  validateContent(step:any, index: number) {
    if (step && this.item) {

    }
  }
}
