import { ThrowStmt } from '@angular/compiler';
import { Component, EventEmitter, Input, OnChanges, Output, TemplateRef } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { PageChangedEvent } from 'ngx-bootstrap/pagination';
import { FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry } from 'ngx-file-drop';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';

@Component({
  selector: 'ibss-editgallery2',
  templateUrl: './editgallery2.component.html',
  styleUrls: ['./editgallery2.component.scss']
})
export class Editgallery2Component implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputParam: any;
  @Input() index: any;
  @Input() isView: boolean= false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  config:typeof CONFIG = CONFIG;
  editItem:any = null;
  activeColor:any =null;
  uploadItem = {
    data:null,
    group:"",
  }
  modalRef: BsModalRef;
  modalConfig = {
    backdrop: true,
    ignoreBackdropClick: false,
    class: 'ibss-image-view modal-lg'
  };

  currImg = 1;
  paginatedImages:any = [];
  constructor(
    private toastr:ToastrService,
    private modalService: BsModalService
  ) { }

  ngOnChanges(): void {
    console.log("EditIntro2Component", this.inputParam);
    this.editItem = JSON.parse(JSON.stringify(this.inputParam));
  }

  updateEditor(close:any) {
    this.inputParam = JSON.parse(JSON.stringify(this.editItem));
    const params: any = {
      component:JSON.parse(JSON.stringify(this.inputParam)),
      action:"saveEdit",
      index:this.index,
      close:close
    };
    this.onAction.emit(params)
  }

  public files: NgxFileDropEntry[] = [];

  public dropped(files: NgxFileDropEntry[]) {
    this.files = files;
    for (const droppedFile of files) {

      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {

          // Here you can access the real file
          console.log(droppedFile.relativePath, file);
          this.readURL(file);
          console.log(this.editItem);

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  readURL(file: any): void {
    if (file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.uploadItem.data = JSON.parse(JSON.stringify(reader.result));
      };
      reader.readAsDataURL(file);
    }
  }

  selectColor(key:any) {
    this.activeColor = {
      key:key,
      value:JSON.parse(JSON.stringify(this.editItem.colors[key]))
    }
  }

  changeComplete(color:any) {
    console.log("changeComplete", color);
    this.activeColor.value = color.color.hex;
    this.editItem.colors[this.activeColor.key] = JSON.parse(JSON.stringify(this.activeColor.value));
    this.updateEditor(false);
  }
  
  onSaveColor() {
    this.activeColor = null;
  }

  onValueChange(event:any) {
    console.log(event);
    console.log(this.editItem);
  }

  onDeleteImage(page:any) {
    const index = page-1;
    const d = JSON.parse(JSON.stringify(this.editItem.images[index]));

    this.editItem.images.splice(index,1);

    this.editItem.groupsTitle = [];
    this.editItem.images.forEach((image:any, index:any) => {

      if (this.editItem.groupsTitle.indexOf(image.group)==-1) {
        this.editItem.groupsTitle.push(image.group);
      }

    });

    let  gIndx = -1 ;

    this.editItem.groups.forEach((group:any, index:any) => {
      if (group.title.toLowerCase() === d.group.toLowerCase()) {
        gIndx = index;
        return;
      }
    });

    if (gIndx!==-1) {
      if (this.editItem.groups[gIndx].count === 1) {
        this.editItem.groups[gIndx];
      } else {
        this.editItem.groups[gIndx].count--;
      }
    }

    if (this.editItem.images.length > 0 ) {
      this.currImg = this.currImg > this.editItem.images.length ? this.editItem.images.length : this.currImg;
      const startItem = (this.currImg - 1) * 1;
      const endItem = this.currImg * 1;
      this.paginatedImages = this.editItem.images.slice(startItem, endItem);
    } else {
      this.modalRef.hide();
    }

    this.updateEditor(false); 
  }

  onUploadImage() {
    const d = JSON.parse(JSON.stringify(this.uploadItem));

    if (d.group && d.group.trim().length > 3 && d.data) {
      
      let gIndx = -1;
      this.editItem.groups.forEach((group:any, index:any) => {
        if (group.title.toLowerCase() === d.group.toLowerCase()) {
          gIndx = index;
          return;
        }
      });

      if (gIndx == -1) {
        this.editItem.groups.push({
          count:1,
          title:d.group
        });
      } else {
        this.editItem.groups[gIndx].count++;
      }

     
      // this.editItem.groups.forEach((group:any, index:any) => {
      //   this.editItem.groupsTitle.push(group.title);
      // });
      
      this.editItem.images.push(d);
      this.editItem.groupsTitle = [];
      this.editItem.images.forEach((image:any, index:any) => {

        if (this.editItem.groupsTitle.indexOf(image.group)==-1) {
          this.editItem.groupsTitle.push(image.group);
        }

      });
      this.uploadItem = {
        group:"",
        data:null
      };

      this.updateEditor(false);

    } else {
      if (!d.group || d.group.trim().length === 0) {
        this.toastr.error("Please enter the Group name");
      } else if ( d.group.trim().length  < 3) {
        this.toastr.error("Group name should be atleast 3 characters length");
      } else if ( !d.data) {
        this.toastr.error("Please choose the image to upload.");
      }
    }
  }

  onReviewImages(template: TemplateRef<any>) {
    this.paginatedImages = this.editItem.images.slice(0, 1);
    this.modalRef = this.modalService.show(template, this.modalConfig);
  }

  pageChanged(event: PageChangedEvent): void {
    const startItem = (event.page - 1) * 1;
    const endItem = event.page * 1;
    this.paginatedImages = this.editItem.images.slice(startItem, endItem);
  }
}
