import { Component, EventEmitter, Input, NgZone, OnChanges, Output } from '@angular/core';
import { FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry } from 'ngx-file-drop';
import { CONFIG } from 'src/app/config';

@Component({
  selector: 'ibss-edit-intro1',
  templateUrl: './edit-intro1.component.html',
  styleUrls: ['./edit-intro1.component.scss']
})
export class EditIntro1Component implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputParam: any;
  @Input() index: any;
  @Input() isView: boolean= false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  config:typeof CONFIG = CONFIG;
  editSlide = {
    selected: "",
    image: null,
    hasCaption:false,
    captions: {
      heading: "",
      content: ""
    }
  }
  editItem:any = null;

  constructor(
    private ngZone:NgZone
  ) { }

  ngOnChanges(): void {
    console.log("EditIntro1Component", this.inputParam);
    this.editItem = JSON.parse(JSON.stringify(this.inputParam.content));
  }

  onAddSlide() {
    const temp = {
      selected: "",
      image: "../../../../assets/images/web/layout1/slide-01.jpg",
      hasCaption:true,
      captions: {
        heading: "Slide Label",
        content: "Nulla vitae elit libero, a pharetra augue mollis interdum."
      }
    }
    this.ngZone.run(()=>{
      this.editItem.push(temp);
      this.editSlide.selected=(this.editItem.length-1).toString();
      this.onSelectSlide();
    });

  }

  onSelectSlide() {

    this.editSlide.image = null,
    this.editSlide.hasCaption = false,
    this.editSlide.captions = {
      heading: "",
      content: ""
    }

    if (this. editSlide.selected && this. editSlide.selected!= "") { 
      const d = JSON.parse(JSON.stringify(this.editItem[Number(this.editSlide.selected)]));
      this.editSlide.image = d.image;
      this.editSlide.hasCaption = d.hasCaption;
      this.editSlide.captions = d.captions;
    }
  }

  public files: NgxFileDropEntry[] = [];

  public dropped(files: NgxFileDropEntry[]) {
    this.files = files;
    for (const droppedFile of files) {

      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {

          // Here you can access the real file
          console.log(droppedFile.relativePath, file);
          this.readURL(file);
          console.log(this.editItem);

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  readURL(file: any): void {
    if (file) {
      const reader = new FileReader();
      reader.onload = e => {
        const editSlide = JSON.parse(JSON.stringify(this.editSlide));
        editSlide.image = reader.result;
        this.editSlide = JSON.parse(JSON.stringify(editSlide));
      };
      reader.readAsDataURL(file);
    }
  }

  onUpdate(close:any) {
    this.ngZone.run(()=>{
      if (this.editSlide.selected && this.editSlide.selected!="") {
        this.editItem[Number(this.editSlide.selected)].image = this.editSlide.image;
        this.editItem[Number(this.editSlide.selected)].hasCaption = this.editSlide.hasCaption;
        this.editItem[Number(this.editSlide.selected)].captions = this.editSlide.captions;
      } else {
        this.editItem.push({
          image :this.editSlide.image,
          hasCaption: this.editSlide.hasCaption,
          captions: this.editSlide.captions
        });
      }
    });
    this.editSlide.selected = "";
    this.updateEditor(false);
  }

  onDelete() {
    this.editItem.splice(Number(this.editSlide.selected),1);
    this.editSlide.selected = "";
    this.updateEditor(false);
  }

  updateEditor(close:any) {
    this.inputParam.content = JSON.parse(JSON.stringify(this.editItem));
    const params: any = {
      component:JSON.parse(JSON.stringify(this.inputParam)),
      action:"saveEdit",
      index:this.index,
      close:close
    };
    this.onAction.emit(params)
  }
}
